:root {
  --primary: #8c54ff;
  --secondary: #f3a847;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

main {
  margin: 5%;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
  border-radius:20%;
}

.navActive {
  color: var(--secondary);
}
